@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');
* {
  margin: 0;
  padding: 0;
  overflow: hidden;
}

@keyframes zoomInOut { 
	0%,100% {
		transform: scale(1);
	}
	50% {
		transform: scale(1.1);
	}
}

.App-header {
  height: 100vh;
  background-position: center;
  background-size: cover;
  background-image: linear-gradient(to bottom, rgba(0,0,0,0.5), rgba(0,0,0,0.5)), url("/src/img/Background.jpg");
  animation: zoomInOut 20s ease infinite;
}

.hero{
  top: 50%;
  left: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
  color: #fff;
  text-align: center;
  font-family: 'Poppins' , sans-serif;
}

h1{
  font-size: 60px;
  letter-spacing: 15px;
}

h4{
  color: #58181F;
  text-align: center;
}

hr {
  width: 50%;
  border: 1.5px solid #fff;
  margin: 30px auto;
}

p{
  font-size: 30px;
  font-weight: bold;
  line-height: 1.5;
  letter-spacing: 1px;
  color: #58181F;
  text-align: center;
}

.social-media a{
  padding: 10px;
}